import { useState, useEffect } from "react";
function Fatigue({ currentFatigue, setCurrentFatigue, setShowSpendFatigue, damage, range, setDamage, setRange }) {

    function spendFatigue(choice) {
        if (choice === "damage") {
            setDamage((damage) => damage + 1);
        } else if (choice === "range") {
            setRange((range) => range + 1);
        }
        setCurrentFatigue((currentFatigue) => currentFatigue - 1);
    }

    // useEffect(() => {
    //     if (currentFatigue <= 0) {
    //         setShowSpendFatigue(false);
    //     }
    // }, [currentFatigue]);


    useEffect(() => {
        dragElement(document.getElementById("fatiguePannel"));
        function dragElement(elmnt) {
            var pos1 = 0,
                pos2 = 0,
                pos3 = 0,
                pos4 = 0;
            if (document.getElementById(elmnt.id + "header")) {
                document.getElementById(elmnt.id + "header").onmousedown =
                    dragMouseDown;
            } else {
                elmnt.onmousedown = dragMouseDown;
            }

            function dragMouseDown(e) {
                e = e || window.event;
                e.preventDefault();
                pos3 = e.clientX;
                pos4 = e.clientY;
                document.onmouseup = closeDragElement;
                document.onmousemove = elementDrag;
            }

            function elementDrag(e) {
                e = e || window.event;
                e.preventDefault();
                pos1 = pos3 - e.clientX;
                pos2 = pos4 - e.clientY;
                pos3 = e.clientX;
                pos4 = e.clientY;
                elmnt.style.top = elmnt.offsetTop - pos2 + "px";
                elmnt.style.left = elmnt.offsetLeft - pos1 + "px";
            }

            function closeDragElement() {
                document.onmouseup = null;
                document.onmousemove = null;
            }
        }
    }, []);
    return (
        <div
            id='fatiguePannel'
            style={{
                position: "absolute",
                backgroundColor: "orange",
                height: "150px",
                width: "250px",
            }}
        >
            <button onClick={() => setShowSpendFatigue(false)}>Exit</button>
            <p>Fatigue Left: {currentFatigue}</p>
            <button onClick={() => spendFatigue("damage")}>Damage</button>
            <button onClick={() => spendFatigue("range")}>Range</button>
        </div>
    );
}

export default Fatigue;